            "Terms And Conditions": ["Terms And Conditions", "Términos y Condiciones", "Termes et conditions", "Geschäftsbedingungen", "規約と条件", "이용 약관", "条款和条件", "Условия и положения", "नियम और शर्तें", "Voorwaarden"],
